  /** Getting PickList DataTypeData information */
  getPickListDataTypeDataFromEntityProperty(entityName, propertyName, callback, onError, config) {
    config.language = config.language || this._getBrowserLanguage(false);
    const cachedToken = `${entityName}_${propertyName}_${config.language}`;
    const cachedResult = this._storage.getItem(this._storagePropertyDataTypeNameSpace, cachedToken);
    if (cachedResult) {
      callback(cachedResult);
    } else {
      return {
        options: {
          select: ['DataTypeData'],
          resourceKind: `entities('${entityName}')/properties`,
          service: 'metadata',
          language: config.language || this._getBrowserLanguage(false),
          filterByLanguage: config.filterByLanguage || false,
          include: ['DataTypeData'],
          where: `propertyName eq '${propertyName}'`,
        },
        handlers: {
          success: (result) => {
            if (result && result.$resources && result.$resources.length > 0) {
              result = result.$resources[0];
            }
            this.parent._storage.setItem(this.parent._storageDataTypeNameSpace, cachedToken, result);
            if (typeof (callback) === 'function') {
              callback(result);
            } else {
              console.log('picklistservice getPickListDataTypeDataFromEntityProperty success: %o', arguments); //eslint-disable-line
            }
          },
          failure: (response) => {
            if (typeof (onError) === 'function') {
              onError(response);
            } else {
              console.log('picklistservice getPickListDataTypeDataFromEntityProperty failure: %o', arguments); //eslint-disable-line
            }
          },
          config: { parent: this, passedIn: config },
        },
      };
    }
  }
